<template lang="html">
  <h1>Planner</h1>
<!-- v-if="user.name=='admin'" -->
  <div class="trenutniDan" >
    <h3 style="text-align:center;">{{trenutniDan.dan}}.{{trenutniDan.mesec}}</h3>

    <label for="exampleFormControlTextarea2">Sastanci</label>
    <textarea class="form-control" id="exampleFormControlTextarea2" rows="5" v-model="trenutniDan.sastanci" ></textarea>


    <label for="exampleFormControlTextarea1">Komentar</label>
    <textarea class="form-control" id="exampleFormControlTextarea1" rows="10" v-model="trenutniDan.komentar" ></textarea>

    <div class="validation">
      {{validation}}
    </div>

    <div class="submitDiv">
    <button class="btnSub" @click="submit">Sačuvaj Izmene</button>
    </div>


  </div>
<div class="headerKalendar">
  <div class="leva">
    <button type="button" class="left" @click="lastMonth">prethodni</button>
  </div>
  <div class="mesec">
    <h2>{{mesec}} 2022.</h2>
  </div>
  <div class="desna">
    <button type="button" class="right" @click="nextMonth">naredni</button>
  </div>
  <p class="radniDan" >PONEDELJAK</p>
  <p class="radniDan">UTORAK</p>
  <p class="radniDan">SREDA</p>
  <p class="radniDan">ČETVRTAK</p>
  <p class="radniDan">PETAK</p>
  <p class="vikend" >SUBOTA</p>
  <p class="vikend" >NEDELJA</p>
</div>

  <div class="kalendar1">

    <div class="dan" v-for="(dan,i) in dani1" v-bind:key="dan.dan">
        <button type="button" class="btn btn-success" v-if="dan.dan!=0" @click="selectTrenutni(i)" style="font-size: x-small;">{{dan.dan}}.</button>
        <p style="font-size: small;">{{dan.sastanci}}</p>
      <!-- <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" v-if="dan.dan!=0" v-model="dan.komentar"></textarea> -->
    </div>
  </div>
  <div class="kalendar1">

    <div class="dan" v-for="(dan,i) in dani2" v-bind:key="dan.dan">
        <button type="button" class="btn btn-success" v-if="dan.dan!=0" @click="selectTrenutni(i+7)" style="font-size: x-small;">{{dan.dan}}.</button>
        <p style="font-size: small;">{{dan.sastanci}}</p>
      <!-- <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" v-if="dan.dan!=0" v-model="dan.komentar"></textarea> -->
    </div>
  </div>
  <div class="kalendar1">

    <div class="dan" v-for="(dan,i) in dani3" v-bind:key="dan.dan">
        <button type="button" class="btn btn-success" v-if="dan.dan!=0" @click="selectTrenutni(i+14)" style="font-size: x-small;">{{dan.dan}}.</button>
        <p style="font-size: small;">{{dan.sastanci}}</p>
      <!-- <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" v-if="dan.dan!=0" v-model="dan.komentar"></textarea> -->
    </div>
  </div>

  <div class="kalendar1">

    <div class="dan" v-for="(dan,i) in dani4" v-bind:key="dan.dan">
        <button type="button" class="btn btn-success" v-if="dan.dan!=0" @click="selectTrenutni(i+21)" style="font-size: x-small;">{{dan.dan}}.</button>
        <p style="font-size: small;">{{dan.sastanci}}</p>
      <!-- <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" v-if="dan.dan!=0" v-model="dan.komentar"></textarea> -->
    </div>
  </div>

  <div class="kalendar1">

    <div class="dan" v-for="(dan,i) in dani5" v-bind:key="dan.dan">
        <button type="button" class="btn btn-success" v-if="dan.dan!=0" @click="selectTrenutni(i+28)" style="font-size: x-small;">{{dan.dan}}.</button>
        <p style="font-size: small;">{{dan.sastanci}}</p>
      <!-- <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" v-if="dan.dan!=0" v-model="dan.komentar"></textarea> -->
    </div>
  </div>


<!--
  <div class="kalendar">
    <div class="dan" v-for="(dan,i) in dani" v-bind:key="dan.dan">
      <button type="button" class="btn btn-success" v-if="dan.dan!=0" @click="selectTrenutni(i)" style="font-size: x-small;">{{dan.dan}}.</button>
      <p style="font-size: small;">{{dan.komentar}}</p>

    </div>
  </div> -->



</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
export default {
  data(){
    return{
      currentDate:{},
      mesec:"",
      mesecBroj:0,
      dani:[],
      brojDana:0,
      prviDanUMesecu:0,
      danUNedelji:0,
      trenutniDan:{},
      godina:2022,
      currentPlanner:{},
      validation:"",
      dani1:[],
      dani2:[],
      dani3:[],
      dani4:[],
      dani5:[],
    }
  },
  computed: mapGetters(["formulari","user","planners"]),
  methods:{
  ...mapActions(['getAllFormulars',"getProfile","submitMesecPlanner","getAllPlanners","putPlanner"]),

  selectTrenutni(i){
    this.trenutniDan=this.dani[i];
  },

  async submit(){
    this.validation="";
    let mesec = this.mesec;
    let godina = this.godina;
    let dani= [];
    for (var i = 0; i < this.dani.length; i++) {
      if(this.dani[i].dan!=0){
        dani.push(this.dani[i])
      }
    }
    let data={
      mesec,
      godina,
      dani,
      id:this.currentPlanner._id,
    }
    await   this.putPlanner(data);
    this.validation=`Uspesno sacuvan mesec ${mesec} ${godina}.`;
    await this.getAllPlanners();
    //this.reload();
  },
  reload(){
    this.dani=[];
    console.log(this.currentPlanner.mesec);
    for (var i = 0; i < this.brojDana+this.prviDanUMesecu; i++) {
      let dan;
      if(i<this.prviDanUMesecu){
        dan={
          mesec:"",
          dan:0,
          komentar:"",
          sastanci:"",
        }
      }else{
        dan={
          mesec:this.mesec,
          dan:i+1-this.prviDanUMesecu,
          komentar:this.currentPlanner.dani[i-this.prviDanUMesecu].komentar,
          sastanci:this.currentPlanner.dani[i-this.prviDanUMesecu].sastanci,
        }
      }

      this.dani.push(dan);
    }
    this.dani1=[];
    this.dani2=[];
    this.dani3=[];
    this.dani4=[];
    this.dani5=[];
    for ( i = 0; i < 7; i++) {
      this.dani1[i]=this.dani[i];
    }
    for ( i = 7; i < 14; i++) {
      this.dani2[i-7]=this.dani[i];
    }
    for ( i = 14; i < 21; i++) {
      this.dani3[i-14]=this.dani[i];
    }
    for ( i = 21; i < 28; i++) {
      this.dani4[i-21]=this.dani[i];
    }
    for ( i = 28; i < this.dani.length; i++) {
      this.dani5[i-28]=this.dani[i];
    }

  },
  nextMonth(){
    this.mesecBroj+=1;

    this.prviDanUMesecu=(this.prviDanUMesecu+this.brojDana)%7;

    if(this.mesecBroj==0){
      this.mesec="Januar";
      this.brojDana=31;
    }
    if(this.mesecBroj==1){
      this.mesec="Februar";
      this.brojDana=28;
    }
    if(this.mesecBroj==2){
      this.mesec="Mart";
        this.brojDana=31;
    }
    if(this.mesecBroj==3){
      this.mesec="April";
        this.brojDana=30;
    }
    if(this.mesecBroj==4){
      this.mesec="Maj";
        this.brojDana=31;
    }
    if(this.mesecBroj==5){
      this.mesec="Jun";
        this.brojDana=30;
    }
    if(this.mesecBroj==6){
      this.mesec="Jul";
        this.brojDana=31;
    }
    if(this.mesecBroj==7){
      this.mesec="Avgust";
        this.brojDana=31;
    }
    if(this.mesecBroj==8){
      this.mesec="Septembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==9){
      this.mesec="Oktobar";
        this.brojDana=31;
    }
    if(this.mesecBroj==10){
      this.mesec="Novembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==11){
      this.mesec="Decembar";
        this.brojDana=31;
    }

    for ( var i = 0; i < this.planners.length; i++) {
      if(this.planners[i].mesec == this.mesec){
        this.currentPlanner=this.planners[i];
      }
    }

    this.reload();



  },
  lastMonth(){

    this.mesecBroj-=1;

    if(this.mesecBroj==0){
      this.mesec="Januar";
      this.brojDana=31;
    }
    if(this.mesecBroj==1){
      this.mesec="Februar";
      this.brojDana=28;
    }
    if(this.mesecBroj==2){
      this.mesec="Mart";
        this.brojDana=31;
    }
    if(this.mesecBroj==3){
      this.mesec="April";
        this.brojDana=30;
    }
    if(this.mesecBroj==4){
      this.mesec="Maj";
        this.brojDana=31;
    }
    if(this.mesecBroj==5){
      this.mesec="Jun";
        this.brojDana=30;
    }
    if(this.mesecBroj==6){
      this.mesec="Jul";
        this.brojDana=31;
    }
    if(this.mesecBroj==7){
      this.mesec="Avgust";
        this.brojDana=31;
    }
    if(this.mesecBroj==8){
      this.mesec="Septembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==9){
      this.mesec="Oktobar";
        this.brojDana=31;
    }
    if(this.mesecBroj==10){
      this.mesec="Novembar";
        this.brojDana=30;
    }
    if(this.mesecBroj==11){
      this.mesec="Decembar";
        this.brojDana=31;
    }

    for (var  i = 0; i < this.planners.length; i++) {
      if(this.planners[i].mesec == this.mesec){
        this.currentPlanner=this.planners[i];
      }
    }

    this.prviDanUMesecu=(this.prviDanUMesecu-this.brojDana)%7;
    if(this.prviDanUMesecu<0){
      this.prviDanUMesecu=this.prviDanUMesecu+7;
    }
    this.reload();



  }

},
async created(){
  await this.getProfile();
  await this.getAllPlanners();
  this.currentDate = new Date();
  this.currentPlanner=this.planners[0];

  if (this.currentDate.getMonth()==0) {
    this.mesec="Januar";
    this.mesecBroj=this.currentDate.getMonth();
    this.brojDana=31;
  }

  this.danUNedelji=this.currentDate.getDay();
  this.prviDanUMesecu=(this.danUNedelji-this.currentDate.getDate())%7;
  if(this.prviDanUMesecu<0){
    this.prviDanUMesecu=this.prviDanUMesecu+7;
  }


  this.reload();

  this.trenutniDan=this.dani[this.prviDanUMesecu-1+this.currentDate.getDate()];
  console.log(this.currentDate);
}
}
</script>

<style lang="css" scoped>
.kalendar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows:  1fr 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:

    ". . . . . . ."
    ". . . . . . ."
    ". . . . . . ."
    ". . . . . . ."
    ". . . . . . .";

    border-style: solid;
    border-color: black;
    border-width: medium;
    padding:10px;

}
.kalendar1 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows:  1fr;
  gap: 0px 0px;
  grid-template-areas:

    ". . . . . . .";

    border-style: solid;
    border-color: black;
    border-width: medium;
    border-bottom: 0px;

}
.headerKalendar{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows:   1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:

    "leva leva mesec mesec mesec desna desna"
    ". . . . . . .";

    border-style: solid;
    border-color: black;
    border-width: medium;
    padding:10px;

    border-bottom: 0px;
}
/* "leva leva mesec mesec mesec desna desna"
". . . . . . ."
0.2fr 0.2fr */
.leva { grid-area: leva; }
.desna {
   grid-area: desna;

 }
.mesec {
  text-align: center;
  grid-area: mesec;
 }
.dan{
  border-style: solid;
  border-color: black;
  border-width: thin;
  padding:10px;
}
.radniDan{
  background-color:#8888ff;
  color:white;
  padding:5px;
  border-style: solid;
  border-color: black;
  border-width: thin;
}
.vikend{
  background-color:#88ff88;
  color:white;
  padding:5px;
  border-style: solid;
  border-color: black;
  border-width: thin;
}
.trenutniDan{
  background-color:#88ff88;
  padding:5px;
  border-style: solid;
  border-color: black;
  border-width: thin;
  width:70%;
  margin: auto;
}

.right{
  background-color: #ffaaaa;
  border-style: solid;
  border-color: black;
  border-width: thin;
  border-radius: 5px;
  float: right;
}
.left{
  background-color: #ffaaaa;
  border-style: solid;
  border-color: black;
  border-width: thin;
  border-radius: 5px;
}

.submitDiv{
  width: 100%;
}
.btnSub{
  width: 100%;
  background-color: #ffaaaa;
  border-style: solid;
  border-color: black;
  border-width: thin;
  border-radius: 5px;
}
</style>
